@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .reasons {
        &_descr {
            width: 300px; }
        &_block {
            align-items: flex-start; } }
    .mobile {
        &_item {
            width: 290px;
            height: 260px; } }
    .footer {
        &_wrapper {
            padding-top: 20px; }
        &_wrapper> div:nth-child(1) {
            width: 20%; }
        &_wrapper> div:nth-child(2) {
            width: 60%; }
        &_wrapper> div:nth-child(3) {
            width: 20%;
            justify-content: center;
            img {
                display: block;
                margin-top: 5px; } } } }

@media (max-width: 991px) {
    .menu {
        &_item {
            padding-right: 14px; }
        &_link {
            font-size: 9px; } }
    .subheader {
        img {
            width: 100%; }
        &_official {
            font-size: 11px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            margin-top: 21px;
            height: 40px;
            width: 100%;
            font-size: 12px; } }
    .promo {
        padding-top: 140px;
        min-height: 600px;
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; }
        &_btn {
            margin-top: 70px; }
        &_descr {
            font-size: 15px; } }
    .reasons {
        &_block {
            flex-direction: column;
            align-items: center; }
        &_descr {
            width: 300px;
            margin-top: 20px; } }
    .mobile {
        &_item {} }
    .choice {
        min-height: 600px;
        .img_car {
            img {
                width: 85%; } } }
    .map {
        min-height: 550px;
        .watch_all {
            bottom: 32px; } }
    .footer {
        &_social {
            margin: 5px auto 25px; }
        &_mobile {
            margin: 15px auto 0; }
        &_wrapper {
            flex-direction: column; }
        &_wrapper > div:nth-child(1) {
            width: 100%; }
        &_wrapper > div:nth-child(2) {
            width: 100%; }
        &_wrapper > div:nth-child(3) {
            width: 100%;
            img {
                display: inline;
                padding: 0; } } } }

@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0,0.8);
        transition: 0.5s all;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none;
            margin: 10px auto; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }

    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            color: #fffffff;
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 10px; } }
    .promo {
        &_header {
            line-height: 45px; } }
    .reasons {
        &_block {
            padding-left: 10%; }
        &_round {
            align-self: center;
            margin-right: 0; }
        &_subtitle {
            min-height: 25px; }
        &_descr {
            width: 100%; } } }
