header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300;
		&:hover {
			text-decoration: none;
			color: $main-color; } }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }
.hambureger {
	display: none; }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0,0,0, .6);
	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	&_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }
	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }
	&_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }
	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $main-color 0%, $btn-color 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase; } }

.promo {
	min-height: 800px;
	background: url('../img/bg/main_bg.jpg') center center/cover no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	&_subheader {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	&_descr {
		color: #fff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $main-color 0%, $btn-color 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase; } }

.small_header {
    display: block;
    background-color: black;
    width: 112px;
    height: 24px;
    margin: 0 auto;
    padding: 2px 12px;
    color: #fff;
    font-size: 12px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center; }
.medium_header {
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center; }
.subtitle {
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center; }
.reasons {
	padding: 71px 0 65px 0;
	&_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }
	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $main-color;
		border-radius: 100%; }
	&_descr {
		width: 343px; }
	&_subtitle {
		line-height: 21px;
		color: $main-color;
		font-size: 17px;
		font-weight: 400;
		min-height: 41px;
		margin-bottom: 3px; }
	&_text {
		color: #202020;
		font-size: 15px;
		font-weight: 300;
		line-height: 21px; } }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url("../img/bg/mobile_app.png") $bg-center;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        &_1 {
            background: url("../img/bg/mobile_screen_1.jpg") $bg-center; }
        &_2 {
            background: url("../img/bg/mobile_screen_2.jpg") $bg-center; }
        &_3 {
            background: url("../img/bg/mobile_screen_3.jpg") $bg-center; }
        &_4 {
            background: url("../img/bg/mobile_screen_4.jpg") $bg-center; }
        &_5 {
            background: url("../img/bg/mobile_screen_5.jpg") $bg-center; }
        &_6 {
            background: url("../img/bg/mobile_screen_6.jpg") $bg-center; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase;
            padding-top: 92px; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 26px;
            height: 26px;
            background-color: #000000;
            border-radius: 100%;
            cursor: pointer; } } }
.small_header_white {
    display: block;
    background-color: #fff;
    width: 112px;
    height: 24px;
    margin: 0 auto;
    padding: 2px 12px;
    color: #000;
    font-size: 12px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center; }
.medium_header_white {
    margin-bottom: 0;
    margin-top: 38px;
    color: #fff;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center; }
.subtitle_white {
    margin-top: 19px;
    color: #fff;
    font-size: 17px;
    font-weight: 400;
    text-align: center;
    padding-bottom: 56px; }
.subtitle_black {
    margin-top: 19px;
    color: #000;
    font-size: 17px;
    font-weight: 400;
    text-align: center; }
.choice {
    position: relative;
    min-height: 895px;
    padding: 57px 0 138px 0;
    background: url("../img/bg/mobile_app.png") center center/cover no-repeat;
    z-index: -2;

    .black_bg {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 499px;
        background-color: #070716;
        z-index: -1; }
    .img_car {
        display: flex;
        justify-content: center; } }

.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		color: #1ca8c3;
		font-size: 22px;
		font-weight: 400; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-size: 17px;
			font-weight: 400;
			line-height: 24px;
			margin-bottom: 10px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background: url('../icons/check_blue.svg') center (center / cover) no-repeat; } } }
	&_descr {
		margin-top: 25px;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		color: $main-color; }
	.warning {
		.require_title {
			color: $main-color; }
		.require_list {
			li {
				&:before {
					background: url('../icons/check_red.svg') center (center / cover) no-repeat; } } } } }
.map {
    position: relative;
    min-height: 717px;
    padding: 49px 0;
    background: url("../img/bg/map.jpg") $bg-center; }
a:hover {
    text-decoration: none;
    color: #fff; }
.watch_all {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 49px;
    text-align: center;
    font-size: 15px;
    font-weight: 400;
    line-height: 68px;
    color: $main-color;
    text-transform: uppercase; }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }
.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: $main-color;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			span {
			    color: $main-color; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: $main-color;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }
.xs-visible {
	display: none; }
